.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--Neutral-20);
  color: var(--Neutral-60);
  font-size: 1.25rem;
  line-height: 1.75rem;
  text-transform: lowercase;
  cursor: pointer;
  animation: opacity-pulse 3s ease-in infinite;
  transition: all 0.2s ease-in-out;
  width: 48px;
  height: 48px;
  border-radius: 18px;
  border: 1px solid rgba(0, 0, 0, 0);
  user-select: none;
  cursor: pointer;

  &:focus {
    border: 2px solid var(--Neutral-20);
    outline: 2px solid var(--Neutral-80);
  }

  &.outlined {
    background: var(--Neutral-2);
    border: 1px solid var(--Neutral-20);
  }

  .no-action {
    pointer-events: none;
  }

  &:hover {
    background: rgba(0, 0, 0, 0);
    border: 1px solid var(--Neutral-20);
  }

  &.connected {
    background: var(--accent-green, #2E7D32);
    color: var(--accent-light-green, #4CAF50);

    &:hover {
      border: 1px solid var(--accent-light-green, #4CAF50);
    }
  }
}

@property --volume {
  syntax: "length";
  inherit: false;
  initial-value: 0px;
}

.disabled .mic-button,
.mic-button.disabled {
  &:before {
    background: rgba(0, 0, 0, 0);
  }
}

.mic-button {
  position: relative;
  background-color: var(--accent-red);
  z-index: 1;
  color: black;
  transition: all 0.2s ease-in;

  &:focus {
    border: 2px solid var(--Neutral-20);
    outline: 2px solid var(--Red-500);
  }

  &:hover {
    background-color: var(--Red-400);
  }

  &:before {
    position: absolute;
    z-index: -1;
    top: calc(var(--volume) * -1);
    left: calc(var(--volume) * -1);
    display: block;
    content: "";
    opacity: 0.35;
    background-color: var(--Red-500);
    width: calc(100% + var(--volume) * 2);
    height: calc(100% + var(--volume) * 2);
    border-radius: 24px;
    transition: all 0.02s ease-in-out;
  }
}

.connect-toggle {
  &:focus {
    border: 2px solid var(--Neutral-20);
    outline: 2px solid var(--accent-green, #2E7D32);
  }

  &:not(.connected) {
    background-color: var(--accent-light-green, #4CAF50);
    color: var(--Neutral-5);
  }
}

.processing-status {
  position: absolute;
  top: -40px;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 1000;
  pointer-events: none;
  
  .status-text {
    display: inline-block;
    background: rgba(0, 0, 0, 0.8);
    color: var(--accent-green);
    padding: 8px 16px;
    border-radius: 4px;
    font-size: 14px;
    border: 1px solid var(--accent-green);
    margin-bottom: 10px;
  }

  .log-line {
    margin: 4px 0;
    font-size: 12px;
    color: var(--accent-green);
    opacity: 0.8;
  }
}

.control-tray {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding-bottom: 18px;

  &.processing {
    padding: 20px;
    border-radius: 27px;
    border: 2px solid var(--accent-green);
    animation: borderPulse 2s ease-in-out infinite;
  }

  .status-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 10px;

    .status-text {
      color: var(--accent-green);
      font-size: 14px;
      display: flex;
      align-items: center;
      gap: 8px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      padding: 0 10px;
    }

    .status-indicator {
      width: 8px;
      height: 8px;
      background-color: var(--accent-green);
      border-radius: 50%;
      animation: pulse 1.5s infinite;
      flex-shrink: 0;
    }
  }

  .disabled .action-button,
  .action-button.disabled {
    background: rgba(0, 0, 0, 0);
    border: 1px solid var(--Neutral-30, #404547);
    color: var(--Neutral-30);
  }

  .connection-container {
    margin-bottom: 10px;
    
    .connection-button-container {
      border-radius: 27px;
      background: var(--Neutral-5);
      padding: 10px;
    }

    .text-indicator {
      font-size: 11px;
      color: var(--accent-light-green, #4CAF50);
      user-select: none;
    }

    &:not(.connected) {
      .text-indicator {
        opacity: 0;
      }
    }
  }

  .text-indicator {
    color: var(--accent-green);
    font-size: 12px;
    margin-top: 5px;
  }
}

.actions-nav {
  background: var(--Neutral-5);
  border: 1px solid var(--Neutral-30);
  border-radius: 27px;
  display: flex;
  gap: 12px;
  align-items: center;
  padding: 10px;

  .action-controls {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .connect-toggle {
    background-color: #2E7D32;
    color: white;
    
    &.connected {
      background-color: var(--Green-700);
    }

    &:not(.connected) {
      background-color: #4CAF50;
    }

    &:focus {
      border: 2px solid var(--Neutral-20);
      outline: 2px solid var(--accent-green);
    }
  }

  .action-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 18px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
}

@keyframes opacity-pulse {
  0% {
    opacity: 0.9;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0.9;
  }
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.4; }
  100% { opacity: 1; }
}

@keyframes borderPulse {
  0% { border-color: rgba(46, 125, 50, 1); }
  50% { border-color: rgba(46, 125, 50, 0.5); }
  100% { border-color: rgba(46, 125, 50, 1); }
}
